<template>
  <div>
      <h1>PharmacieDetail</h1>
        <div class="card">
            <div>
                <label>id:</label>
            </div>  
            <div>
                <label>Nom de la Pharmacie:</label>
                <input type="text" v-model="pharmacie.nom">
            </div>                      
            <div>
                <label>Localisation:</label>
                <input type="text" v-model="pharmacie.localisation">
            </div>
            <div>
                <label>Quartier:</label>
                <input type="text" v-model="pharmacie.quartier">
            </div>
            <div>
                <label>Localisation dans le quartier:</label>
                <input type="text" v-model="pharmacie.localisationDansQuartier">
            </div>
            <div>
                <h4>Coordonnées géographiques:</h4>
                <label>Latitude: </label>
                <input type="text" v-model="pharmacie.latitude">
                <label>Longitude: </label>
                <input type="text" v-model="pharmacie.longitude">
            </div>
            <div>
                <label>Boite Postale:</label>
                <input type="text" v-model="pharmacie.boitePostale">
            </div>
            <div>
                <label>Téléphone:</label>
                <input type="text" v-model="pharmacie.telephone">
            </div>
            <div>
                <label>E-mail:</label>
                <input type="text" v-model="pharmacie.email">
            </div>
            <div>
                <label>Site web:</label>
                <input type="text" v-model="pharmacie.siteWeb">
            </div>
            <div>
               <label>Heure ouverture:<input type="time" v-model="pharmacie.heureOuverture"> </label>
               <label>Heure fermeture:<input type="time" v-model="pharmacie.heureFermeture"> </label>
            </div>
            <b-button pill variant="success" @click="pharmacieSave(pharmacie)">Save</b-button> 
            <b-button pill variant="dark" @click="pharmacieCancel()">Cancel</b-button>
     </div>
  </div>
</template>

<script>
export default {
    name:'PharmacieDetail',

    data(){
        return {
            pharmacie: this.$store.getters.getDetailPharmacie
        }
    },

    methods: {
        pharmacieSave(pharmacie){
            this.$store.commit('setDetailPharmacie',pharmacie);
            this.pharmacieCancel();
        },
        pharmacieCancel(){
            this.$router.push("/pharmacie-dashboard/");
        }
    }
}
</script>

